#header {
  position: relative;
  color: #333333;
  padding: 0.9375rem 0;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(100% + 1.75rem);
    background-image: url("../img/header-bg.svg");
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: cover;
    pointer-events: none;
  }
  .container-fluid {
    z-index: 2;
  }
  .header__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1.5rem;
  }
  .header__left {
    display: flex;
    align-items: center;
    gap: 1rem;
  }
  .header__logo {
    width: 7.5rem;
    flex-shrink: 0;
    img {
      width: 100%;
    }
  }
  .header__text {
    max-width: 23.125rem;
    strong {
      font-family: "Arkhip";
      font-size: 1.25rem;
      line-height: 130%;
      text-transform: uppercase;
    }
    p {
      font-size: 1rem;
      line-height: 130%;
      margin: 1rem 0 0 0;
    }
  }
  .header__right {
    flex-shrink: 0;
    display: flex;
    gap: 3.125rem;
  }
  .header__wrap {
    display: flex;
    gap: 3.5rem;
  }
  .header__location {
    display: flex;
    gap: 0.875rem;
    &:hover {
      i {
        background: #00148B;
        svg path {
          stroke: #fff;
        }
      }
    }
    i {
      width: 2.25rem;
      height: 2.25rem;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #fff;
      border-radius: 50%;
      svg {
        width: 1.125rem;
        height: auto;
      }
    }
    p {
      font-size: 1rem;
      line-height: 130%;
      margin: 0;
    }
    p strong {
      display: block;
      font-size: 1.125rem;
      font-weight: 700;
      margin-bottom: 0.1875rem;
    }
  }
  .header__website {
    font-size: 1.125rem;
    line-height: 130%;
    border-bottom: 0.0625rem solid transparent;
    &:hover {
      color: #00148B;
      border-color: #00148B;
    }
  }
  .header__btn {
    display: block;
    background: transparent;
    border-bottom: 0.0625rem solid #00148b;
    font-size: 0.875rem;
    line-height: 130%;
    color: #00148B;
    margin-top: 0.3125rem;
    &:hover {
      border-color: #8FA8CC;
      color: #8FA8CC;
    }
    span {
      min-width: unset;
      padding: 0;
    }
  }
  .header__socials {
    display: flex;
    align-items: center;
    gap: 0.75rem;
  }
  .header__social {
    width: 2.5rem;
    &:hover {
      opacity: 0.7;
    }
    img {
      width: 100%;
    }
  }
  .header__phone {
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 700;
    &:hover {
      color: #00148B;
    }
  }
  .header__btn2 {
    display: block;
    background: transparent;
    margin-top: 0.5rem;
    &:hover {
      color: #00148b;
      span {
        border-color: #00148b;
      }
    }
    span {
      min-width: unset;
      border-bottom: 0.0625rem dashed #333333;
      font-size: 0.875rem;
      line-height: 130%;
      font-weight: 700;
      padding: 0;
      transition: all 0.3s;
    }
  }
}
.burger {
  position: relative;
  z-index: 15;
  width: 2rem;
  height: 1.5625rem;
  cursor: pointer;
  display: none;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 0.1875rem;
    background: #333333;
    border-radius: 0.1875rem;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span {
      background: #Fff;
    }
    span:nth-child(1) {
      transform: rotate(-45deg);
      top: 0.6875rem;
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      transform: rotate(45deg);
      bottom: 0.65625rem;
    }
  }
}

@media screen and (max-width: 1500px) {
  #header {
    .header__right {
      gap: 2rem;
    }
    .header__wrap {
      gap: 2rem;
    }
  }
}

@media screen and (max-width: 1300px) {
  #header {
    .header__logo {
      width: 6.25rem;
    }
    .header__text {
      max-width: 20rem;
      strong {
        font-size: 1rem;
      }
      p {
        font-size: 0.875rem;
        margin-top: 0.5rem;
      }
    }
    .header__wrap:nth-child(2) {
      flex-direction: column;
      gap: 0.5rem;
    }
    .header__socials {
      order: 1;
    }
  }
}

@media screen and (max-width: 1023px) {
  #header {
    .header__right {
      position: fixed;
      z-index: 10;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      flex-direction: column;
      align-items: center;
      background: radial-gradient(121.76% 103.77% at 28.999999999999996% 8%,rgb(0, 20, 139),rgb(25, 51, 81) 68.447%,rgb(0, 0, 0) 100%);
      color: #Fff;
      padding: 6.25rem 1rem 3.125rem;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
    }
    .header__wrap {
      flex-direction: column;
      align-items: center;
    }
    .header__location {
      &:hover {
        i {
          background: #8FA8CC;
        }
      }
    }
    .header__website {
      &:hover {
        color: #8FA8CC;
        border-color: #8FA8CC;
      }
    }
    .header__btn {
      color: #fff;
      border-color: #fff;
      &:hover {
        color: #8FA8CC;
      }
    }
    .header__phone {
      &:hover {
        color: #8FA8CC;
      }
    }
    .header__btn2 {
      width: 100%;
      color: #fff;
      &:hover {
        color: #8FA8CC;
        span {
          border-color: #8FA8CC;
        }
      }
      span {
        display: inline-block;
        border-color: #fff;
      }
    }
  }
  .burger {
    display: block;
  }
}

@media screen and (max-width: 576px) {
  #header {
    padding: 0.625rem 0;
    &:after {
      height: calc(100% + 0.5rem);
    }
    .header__left {
      gap: 0.5rem;
    }
    .header__logo {
      width: 2.8125rem;
    }
    .header__text {
      strong {
        font-size: 0.625rem;
      }
      p {
        font-size: 0.625rem;
        margin-top: 0;
      }
    }
    .header__location {
      gap: 0.5rem;
      i {
        width: 24px;
        height: 24px;
        svg {
          width: 0.75rem;
        }
      }
      p {
        font-size: 0.75rem;
        strong {
          font-size: 0.875rem;
        }
      }
    }
    .header__website {
      font-size: 0.75rem;
    }
    .header__btn {
      font-size: 0.75rem;
    }
    .header__phone {
      font-size: 0.875rem;
    }
    .header__btn2 {
      span {
        font-size: 0.75rem;
      }
    }
    .header__social {
      width: 2rem;
    }
  }
  .burger {
    width: 1.25rem;
    height: 1rem;
    span {
      height: 0.125rem;
    }
    &--active {
      span:nth-child(1) {
        top: 0.4375rem;
      }
      span:nth-child(3) {
        bottom: 0.4375rem;
      }
    }
  }
}

@media screen and (max-width: 359px) {
  #header {
    background: #fff;
    &:after {
      display: none;
    }
  }
}