h2 {
  font-size: 2.5rem;
  line-height: 130%;
  margin: 0;
}
.btn-red {
  position: relative;
  background: transparent;
  &:hover {
    span:nth-child(2) {
      &:before {
        background: radial-gradient(27.00% 41.00% at 50% 107%,rgb(255, 255, 255),rgb(255, 0, 0) 100%),radial-gradient(68.00% 107.00% at 50% 107%,rgb(242, 35, 35),rgb(216, 12, 12) 100%),rgba(255, 255, 255, 0.08),rgb(0, 20, 139);
      }
    }
  }
  span {
    min-width: 21.25rem;
    padding: 1.75rem;
    font-size: 1rem;
    line-height: 130%;
    text-transform: uppercase;
    color: #fff;
  }
  span:nth-child(1) {
    z-index: 2;
  }
  span:nth-child(2) {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(255, 255, 255, 0.08);
      clip-path: polygon( 91.943% 99.351%,0.147% 99.351%,0.147% 0.649%,99.853% 0.649%,99.853% 67.215%,91.943% 99.351% );
      pointer-events: none;
    }
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: calc(100% - 0.125rem);
      height: calc(100% - 0.125rem);
      backdrop-filter: blur(30px);
      background: radial-gradient(87.00% 136.00% at 50% 107%,rgb(223, 27, 27),rgb(139, 0, 0) 100%),rgba(255, 255, 255, 0.08),rgb(0, 20, 139);
      clip-path: polygon( 91.943% 99.351%,0.147% 99.351%,0.147% 0.649%,99.853% 0.649%,99.853% 67.215%,91.943% 99.351% );
      pointer-events: none;
    }
  }
  span:nth-child(3) {
    position: absolute;
    left: 50%;
    bottom: -0.3125rem;
    transform: translate(-50%, 0);
    width: calc(100% - 0.125rem);
    height: 70%;
    background: rgb(232, 2, 2);
    filter: blur(1.875rem);
  }
}
@keyframes gif {
  0% {
    background: #e62331;
  }
  50% {
    background: #fff;
  }
  100% {
    background: #e62331;
  }
}
.swiper-button-next {
  width: 4rem;
  height: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #333333;
  clip-path: polygon( 67.213% 0%,0% 0%,0% 100%,100% 100%,100% 33.607%,67.213% 0% );
  margin-top: 0;
  &:after {
    display: none;
  }
  svg {
    width: 0.5rem;
    height: auto;
  }
}
.swiper-button-prev {
  width: 4rem;
  height: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #333333;
  clip-path: polygon( 32.787% 0%,100% 0%,100% 100%,0% 100%,0% 33.607%,32.787% 0% );
  margin-top: 0;
  &:after {
    display: none;
  }
  svg {
    width: 0.5rem;
    height: auto;
  }
}

@media screen and (max-width: 1399px) {
  h2 {
    font-size: 2rem;
  }
}

@media screen and (max-width: 1023px) {
  h2 {
    font-size: 1.5rem;
  }
  .btn-red {
    span {
      min-width: 14.0625rem;
      font-size: 0.875rem;
      padding: 1rem 1.25rem;
    }
  }
  .swiper-button-prev,
  .swiper-button-next {
    width: 3.125rem;
    height: 3.125rem;
  }
}

@media screen and (max-width: 576px) {
  h2 {
    font-size: 1.25rem;
  }
}

@media screen and (max-width: 499px) {
  .container-fluid {
    padding: 0 1rem !important;
  }
}

@media screen and (max-width: 374px) {
  .btn-red {
    span {
      min-width: 11.25rem;
    }
  }
}