.section-group {
  background: radial-gradient(121.76% 103.77% at 28.999999999999996% 8%,rgb(0, 20, 139),rgb(25, 51, 81) 68.447%,rgb(0, 0, 0) 100%);
}
.main-section {
  color: #fff;
  overflow: hidden;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);
    width: calc(100% - 3.5rem);
    height: 100%;
    background-image: url("../img/main-img.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    clip-path: polygon(6% 0, 100% 0, 100% 90%, 94% 100%, 25% 100%, 0 100%, 0 12%);
    pointer-events: none;
  }
  .container-fluid {
    z-index: 3;
  }
  &__inner {
    position: relative;
    padding: 3.75rem 0 7.5rem;
    .main-section__img2 {
      display: none;
    }
  }
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 1rem;
  }
  h1 {
    position: relative;
    font-size: 6.25rem;
    line-height: 105%;
    letter-spacing: 0.0625rem;
    text-transform: uppercase;
    margin: 0;
    i {
      position: absolute;
      top: -0.75rem;
      left: 20.5rem;
      width: 5.3125rem;
      img {
        width: 100%;
      }
    }
    span {
      position: absolute;
      top: 4.46rem;
      left: 2.45rem;
      width: 0.875rem;
      height: 0.875rem;
      background: #e62331;
      border-radius: 50%;
      animation: gif 2s infinite linear;
    }
  }
  .rec {
    position: relative;
    display: flex;
    align-items: center;
    gap: 1rem;
    padding-right: 1.25rem;
    font-family: "Arkhip";
    font-size: 2rem;
    line-height: 105%;
    text-transform: uppercase;
    color: #e62331;
    &:after {
      content: '';
      position: absolute;
      right: 0;
      bottom: -1rem;
      width: 0.0625rem;
      height: 9.0625rem;
      background: rgb(255, 255, 255, 0.45);
      pointer-events: none;
    }
    &:before {
      content: '';
      position: absolute;
      right: 0;
      bottom: 8.0625rem;
      width: 18.75rem;
      height: 0.0625rem;
      background: rgb(255, 255, 255, 0.45);
      pointer-events: none;
    }
    span {
      width: 2.5rem;
      height: 2.5rem;
      background: #e62331;
      border-radius: 50%;
      margin-top: -0.125rem;
      animation: gif 2s infinite linear;
    }
  }
  &__subtitle {
    max-width: 65.9375rem;
    font-family: "Arkhip";
    font-size: 2.5rem;
    line-height: 130%;
    letter-spacing: 0.0625rem;
    margin-top: 0.75rem;
    span {
      color: #4F93F4;
    }
  }
  &__items {
    max-width: 56.25rem;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 1.25rem / 2));
    gap: 1.25rem;
    margin-top: 3.125rem;
    margin-left: 12.1875rem;
  }
  &__item {
    position: relative;
    display: flex;
    align-items: center;
    gap: 0.875rem;
    padding: 1.875rem;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 9, 87, 0.2);
      backdrop-filter: blur(1.875rem);
      pointer-events: none;
    }
    &:nth-child(1) {
      &:after {
        clip-path: polygon( 0.114% 99.573%,0.114% 14.251%,6.48% 0.427%,99.886% 0.427%,99.886% 85.968%,94.505% 99.573%,0.114% 99.573% );
      }
    }
    &:nth-child(2) {
      &:after {
        clip-path: polygon( 99.886% 99.573%,99.886% 14.251%,93.52% 0.427%,0.114% 0.427%,0.114% 85.968%,5.495% 99.573%,99.886% 99.573% );
      }
    }
    &:nth-child(3) {
      &:after {
        clip-path: polygon( 99.886% 99.573%,99.886% 14.251%,93.52% 0.427%,0.114% 0.427%,0.114% 85.968%,5.495% 99.573%,99.886% 99.573% );
      }
    }
    &:nth-child(4) {
      &:after {
        clip-path: polygon( 0.114% 99.573%,0.114% 14.251%,6.48% 0.427%,99.886% 0.427%,99.886% 85.968%,94.505% 99.573%,0.114% 99.573% );
      }
    }
    &-icon {
      position: relative;
      z-index: 1;
      max-width: 3rem;
    }
    &-text {
      position: relative;
      z-index: 1;
      font-size: 1rem;
      line-height: 130%;
      strong {
        display: block;
        font-weight: 600;
        color: #4F93F4;
      }
    }
  }
  &__group {
    display: flex;
    gap: 0.75rem;
    margin: 2rem 0 0 18.4375rem;
  }
  &__socials {
    display: flex;
    gap: 0.75rem;
  }
  &__social {
    width: 2.5rem;
    &:hover {
      opacity: 0.7;
    }
    img {
      width: 100%;
    }
  }
  &__info {
    position: relative;
    display: inline-block;
    font-size: 1.125rem;
    line-height: 130%;
    padding: 0.875rem 2.5rem 0.75rem 4rem;
    margin: 2.5rem 0 0 36.5625rem;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(255, 255, 255, 0.08);
      backdrop-filter: blur(1.875rem);
      clip-path: polygon( 92.306% 98.769%,0.336% 98.769%,6.942% 70.105%,6.985% 69.915%,6.985% 69.62%,6.985% 39.512%,14.878% 0.633%,99.661% 0.633%,99.661% 68.086%,92.306% 98.769% );
      pointer-events: none;
    }
    strong {
      position: relative;
      z-index: 1;
      display: block;
      font-weight: 700;
      color: #4F93F4;
    }
    span {
      position: relative;
      z-index: 1;
    }
  }
  &__img {
    position: absolute;
    z-index: 4;
    right: 3.5rem;
    bottom: 0;
    pointer-events: none;
    img:nth-child(1) {
      width: 39.4vw;
      max-width: 47.25rem;
    }
    img:nth-child(2) {
      position: absolute;
      left: 4.375rem;
      bottom: 4.375rem;
      width: 36.5vw;
      max-width: 43.75rem;
    }
  }
  &__img2 {
    position: absolute;
    z-index: 3;
    left: 11.125rem;
    bottom: -1rem;
    max-width: 23.4375rem;
    img {
      width: 100%;
    }
  }
  &__img3 {
    position: absolute;
    z-index: 1;
    top: 11.5625rem;
    left: 0;
    pointer-events: none;
    img:nth-child(1) {
      max-width: 8.3125rem;
      width: 100%;
    }
    img:nth-child(2) {
      position: absolute;
      z-index: 1;
      top: 5rem;
      left: 4.3125rem;
      max-width: 60.625rem;
      max-height: unset;
    }
  }
  &__img4 {
    display: none;
  }
}
.services {
