      position: relative;
      z-index: 1;
      max-width: 3.3125rem;
      flex-shrink: 0;
    }
    p {
      position: relative;
      z-index: 1;
      font-size: 1.125rem;
      line-height: 130%;
      font-weight: 500;
      margin: 0;
    }
    .line {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      width: 100%;
      height: 0.0625rem;
      background: rgba(255, 255, 255, 0.22);
      pointer-events: none;
