    display: flex;
    flex-direction: column;
    gap: 1.875rem;
    padding: 2.5rem 1.5rem 2.5rem 2.5rem;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
