    }
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: calc(100% - 0.125rem);
      height: calc(100% - 0.125rem);
      clip-path: polygon( 15.357% 0%,0% 12.793%,0% 100%,95.036% 100%,100% 94.868%,100% 0%,15.357% 0% );
      background: rgb(0, 20, 139);
      backdrop-filter: blur(1.875rem);
      pointer-events: none;
    }
    li {
      position: relative;
      z-index: 2;
      display: flex;
      gap: 0.875rem;
    }
    li i {
      width: 2.9375rem;
      flex-shrink: 0;
    }
    li p {
      font-size: 1.125rem;
      line-height: 130%;
      font-weight: 600;
      margin: 0;
    }
  }
  .rec {
    position: absolute;
    top: 2.125rem;
    right: 2.125rem;
    display: flex;
    align-items: center;
    gap: 1rem;
    padding-right: 1.25rem;
    font-family: "Arkhip";
    font-size: 1.5rem;
