    &:after {
      content: '';
      position: absolute;
      top: -0.875rem;
      right: 0;
      width: 0.0625rem;
      height: 6.25rem;
      background: rgb(255, 255, 255, 0.45);
      pointer-events: none;
    }
    &:before {
      content: '';
      position: absolute;
      top: -0.875rem;
      right: 0;
      width: 12.5rem;
      height: 0.0625rem;
      background: rgb(255, 255, 255, 0.45);
      pointer-events: none;
    }
    span {
      width: 2rem;
      height: 2rem;
      background: #e62331;
      border-radius: 50%;
      animation: gif 2s infinite linear;
    }
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    top: -0.375rem;
    right: calc(100% - 4rem);
    max-width: 27rem;
    width: 23vw;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &__wrap2 {
    position: relative;
    z-index: 2;
    display: flex;
    align-items: stretch;
    margin-top: 12.375rem;
  }
  &__subtitle {
    position: relative;
    max-width: 37.5rem;
    font-family: "Arkhip";
    font-size: 1.75rem;
    line-height: 130%;
    padding: 1.875rem;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(255, 255, 255, 0.08);
      backdrop-filter: blur(0.5rem);
      clip-path: polygon( 100% 0%,0% 0%,0% 71.739%,3.103% 100%,95.167% 100%,95.167% 28.261%,100% 0% );
