    }
  }
  &__group {
    position: relative;
    display: flex;
    align-items: center;
    gap: 2rem;
    padding: 1.875rem 3.75rem;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(255, 255, 255, 0.08);
      backdrop-filter: blur(0.5rem);
      clip-path: polygon( 3.205% 0%,100% 0%,100% 71.739%,98.077% 100%,0% 100%,0% 28.261%,3.205% 0% );
      pointer-events: none;
    }
  }
  &__info {
    position: relative;
    z-index: 1;
    font-size: 1.125rem;
    line-height: 130%;
    font-weight: 700;
    span {
      display: block;
      color: rgb(143, 168, 204);
    }
  }
  &__btn {
    position: relative;
    z-index: 1;
  }
}
.price {
