}
.quiz {
  width: 100%;
  min-height: 31.5625rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.22);
  backdrop-filter: blur(1.875rem);
  padding: 3.125rem 3.75rem;
  &-step {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    pointer-events: none;
  }
  &-step--active {
    position: static;
    opacity: 1;
    pointer-events: auto;
  }
  &-title {
    text-align: center;
    font-size: 1.875rem;
    line-height: 130%;
    font-weight: 500;
  }
  &-checkboxes {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 0.9375rem / 2));
    gap: 0.9375rem;
    margin-top: 2rem;
  }
  &-checkbox {
    position: relative;
    padding: 0.75rem 0.75rem 0.75rem 1.25rem;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: url("../img/quiz-checkbox__bg.svg");
      background-repeat: no-repeat;
      background-position: right bottom;
      background-size: cover;
      pointer-events: none;
    }
    .line {
      position: absolute;
      top: 0;
      left: 0;
      width: calc(100% - 0.125rem);
      height: 0.0625rem;
      background: rgba(255, 255, 255, 0.28);
      pointer-events: none;
    }
    .line2 {
      position: absolute;
      top: 0;
      left: 0;
      width: 0.0625rem;
      height: 100%;
      background: rgba(255, 255, 255, 0.28);
      pointer-events: none;
    }
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      cursor: pointer;
    }
    input:checked ~ label {
      &:after {
        background: linear-gradient(116.06deg, rgb(34, 158, 217) -2.775%,rgb(0, 20, 139) 89.034%);
        border-color: transparent;
      }
    }
    label {
      position: relative;
      font-size: 0.875rem;
      line-height: 130%;
      font-weight: 500;
      padding-left: 2.75rem;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 1.25rem;
        height: 1.25rem;
        border: 0.0625rem solid rgba(255, 255, 255, 0.28);
        border-radius: 50%;
      }
    }
  }
  &-input {
    max-width: 13.4375rem;
    margin-top: 2rem;
    input {
      width: 100%;
      background: transparent;
      border: none;
      border-bottom: 0.0625rem solid rgba(255, 255, 255, 0.28);
      font-size: 0.875rem;
      line-height: 130%;
      color: rgba(255, 255, 255, 1);
      &::placeholder {
        color: rgba(255, 255, 255, 0.5);
      }
    }
    input[type="number"]::-webkit-outer-spin-button,
    input[type="number"]::-webkit-inner-spin-button {
      -webkit-appearance: none;
    }

    input[type='number'],
    input[type="number"]:hover,
    input[type="number"]:focus {
      appearance: none;
      -moz-appearance: textfield;
    }
  }
  &-nav {
    margin-top: 2rem;
  }
  &__question-nav {
    display: flex;
    align-items: center;
    gap: 1.25rem;
  }
  &__question-number {
    font-size: 1rem;
    line-height: 105%;
  }
  .percent {
    font-size: 1rem;
    line-height: 105%;
    color: rgb(143, 168, 204);
  }
  .progress-bar {
    position: relative;
    width: 100%;
    height: 0.5rem;
    background: rgba(255, 255, 255, 0.2);
    margin-top: 2rem;
    span {
      position: absolute;
      top: 0;
      left: 0;
      width: 0;
      height: 100%;
      background: linear-gradient(116.06deg, rgb(34, 158, 217) -2.775%,rgb(0, 20, 139) 89.034%);
      transition: all 0.3s;
    }
  }
  &-btns {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 0.625rem;
    margin-top: 2rem;
  }
  &-prev {
    width: 3.75rem;
    height: 3.75rem;
    flex-shrink: 0;
    background: rgba(255, 255, 255, 0.1);
    clip-path: polygon(26% 0, 100% 0, 100% 100%, 0 100%, 0 26%);
    pointer-events: none;
  }
  &-prev--active {
    pointer-events: auto;
  }
  &-next,
  &-form__btn {
    width: 100%;
    max-width: 17.625rem;
    flex-grow: 1;
    &:after {
      content: '';
      position: absolute;
      z-index: 2;
      top: 50%;
      right: 1.5rem;
      transform: translate(0, -50%);
      width: 0.5rem;
      height: 0.5rem;
      background-image: url("../img/arrow.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
    }
    span {
      min-width: unset !important;
      padding: 1.25rem 2.5rem 1.25rem 1.5rem !important;
    }
  }
  &-form__btn {
    display: none;
    &:after {
      display: none;
    }
  }
  &-form__btn--active {
    display: block;
  }
  .remove {
    display: none;
  }
  .quiz-error {
    display: none;
    font-size: 1rem;
    line-height: 130%;
    color: #e62331;
    margin-top: 0.625rem;
    &--active {
      display: block;
    }
  }
}
.advantages {
  z-index: 1;
  background: #fff;
  padding: 0 0 6.25rem 0;
  &__items {
    max-width: 63.625rem;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 1.875rem / 2));
    gap: 1.875rem;
  }
  h2 {
    font-size: 2.25rem;
    color: rgb(51, 51, 51);
    .text_color {
      display: block;
