      img:nth-child(1) {
        width: 7.625rem;
      }
      img:nth-child(2) {
        position: absolute;
        top: -2.5rem;
        right: 4rem;
        max-width: unset;
        max-height: unset;
        width: 37rem;
      }
    }
  }
  .services {
    padding: 1.875rem 0;
    &__tabs {
      margin-top: 1.25rem;
    }
    &__wrap {
      gap: 1.25rem;
      margin-top: 1.25rem;
    }
    &__right {
      max-width: 24rem;
      margin-left: auto;
    }
    ul {
      li p {
        font-size: 0.75rem;
        font-weight: 500;
      }
    }
    &__img2 {
      right: calc(100% - 4rem);
      max-width: 7.5rem;
    }
    &__subtitle {
      font-size: 1rem;
      padding: 1.125rem 3rem 1.125rem 1.25rem;
    }
    &__group {
      padding: 1.25rem 1.875rem 1.25rem 2.5rem;
    }
  }
  .price {
    padding: 1.875rem 0;
    &__inner {
      gap: 1.25rem;
      background-image: url("../img/price-bg2.webp");
      padding-top: 1.875rem;
      &:after {
        width: 4rem;
        height: 4rem;
      }
    }
    &__img {
      left: 1rem;
      transform: unset;
    }
    &__img2 {
      left: -2rem;
      transform: unset;
    }
    &__img3 {
      left: unset;
      right: 0;
      transform: unset;
    }
  }
  .advantages {
    h2 {
      font-size: 1.25rem;
      .text_color {
        font-size: 2.5rem;
      }
    }
    &__items {
      gap: 0.625rem;
    }
    &__item {
      gap: 1.25rem;
      padding: 1.25rem;
      &-text {
        margin-top: 0.625rem;
      };
    }
    &__group {
      margin-top: 3.125rem;
    }
    &__img {
      z-index: 1;
      right: -0.625rem;
      max-width: 10rem;
    }
  }
  .installation {
    padding: 2.5rem 0 1rem;
    &__btn {
      margin-top: 1.25rem;
    }
    &__img {
      position: relative;
      right: unset;
      bottom: unset;
      transform: unset;
      max-width: 25rem;
      margin: 3.125rem auto 0;
    }
    &__img2 {
      bottom: 16rem;
      max-width: 12rem;
    }
  }
  .info {
    padding-top: 1.875rem;
    h2 {
      text-align: start;
    }
  }
  .info2 {
    padding: 1.875rem 0 0.9375rem;
  }
  .cases {
    padding-top: 0.9375rem;
    &__slider {
      margin-top: 1.25rem;
    }
    .swiper-slide {
      max-width: 17.8125rem;
    }
  }
  .case-card {
    padding: 1.25rem;
    &:after,
    &:before {
      clip-path: polygon(93% 0, 100% 6%, 100% 100%, 0 100%, 0 0);
    }
    &__desc {
      margin-top: 1.25rem;
    }
    &__title {
      font-size: 1rem;
    }
    &__items {
      margin-top: 1.25rem;
    }
    &__subtitle {
      font-size: 0.875rem;
    }
    ul {
      li {
        p {
          font-size: 0.75rem;
        }
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .installation {
    &__group {
      max-width: 18.75rem;
      margin-right: -1.25rem;
    }
  }
}

@media screen and (max-width: 399px) {
  .main-section {
    &__img4 {
      img:nth-child(2) {
        width: 24rem;
      }
    }
  }
  .services {
    &__right {
      max-width: 17.875rem;
    }
    &__img2 {
      right: calc(100% - 2rem);
    }
  }
  .price {
    &__inner {
      padding-left: 1.25rem;
      padding-right: 1.25rem;
    }
    &__img3 {
      right: -6rem;
    }
  }
  .quiz {
    padding: 1.25rem;
  }
  .installation {
    &__img {
