      width: calc(100% + 2rem);
    }
  }
}

@media screen and (max-width: 374px) {
  .info {
    &__wrap {
      width: 345px;
    }
    &__img {
      max-width: 18.75rem;
      width: 100%;
      margin: 0 auto;
    }
    &__item {
      &:nth-child(1) {
        .line2 {
          left: 1.6rem;
          bottom: -11.5rem;
          transform: rotate(31deg);
        }
      }
      &:nth-child(2) {
        .line2 {
          right: 5.8rem;
          bottom: -10.8rem;
          transform: rotate(53deg);
          height: 7rem;
        }
      }
      &:nth-child(3) {
        .line {
          top: -5.3rem;
          left: 1.5rem;
          transform: rotate(8deg);
        }
        .line2 {
          top: -7.4rem;
          left: 17px;
          right: unset;
          transform: rotate(-45deg);
          width: 6rem;
        }
      }
      &:nth-child(4) {
        .line2 {
          top: -11.4rem;
         right: 2.6rem;
          transform: rotate(27deg);
          width: 10rem;
        }
      }
    }
  }
}

@media screen and (max-width: 370px) {
  .main-section {
    &__img {
      img:nth-child(1) {
        max-width: 12rem;
      }
    }
    &__img2 {
      max-width: 10rem;
    }
  }
  .services {
    &__img2 {
      right: calc(100% - 4rem);
    }
  }
